import React from 'react'
import TabHead from '../Sections/TabHead'
import Widget from '../widgets/Widget'
import { toast } from 'react-toastify'
import axios from 'axios'
import { useState } from "@wordpress/element"
import '../../assets/scss/tabs.scss'

const WidgetTabs = ({
  addons,
  handleChange
}) => {
  // State to track the active tab
  const [activeTab, setActiveTab] = useState('shop');
  const isProActive = Boolean(pure_wc_shopbuild.is_pro_active)

  const ShopWidgets = Object.entries(addons).filter(([key, value]) => value?.category.toLowerCase() === "shop");
  const ShopSingleWidgets = Object.entries(addons).filter(([key, value]) => value?.category.toLowerCase() === "single");
  const CartWidgets = Object.entries(addons).filter(([key, value]) => value?.category.toLowerCase() === "cart");
  const CheckoutWidgets = Object.entries(addons).filter(([key, value]) => value?.category.toLowerCase() === "checkout");
  const BannerWidgets = Object.entries(addons).filter(([key, value]) => value?.category.toLowerCase() === "banner");
  const HomeWidgets = Object.entries(addons).filter(([key, value]) => value?.category.toLowerCase() === "home");
  const OfferWidgets = Object.entries(addons).filter(([key, value]) => value?.category.toLowerCase() === "offer");
  const MyAccountsWidgets = Object.entries(addons).filter(([key, value]) => value?.category.toLowerCase() === "myaccount");
  const ThankYouWidgets = Object.entries(addons).filter(([key, value]) => value?.category.toLowerCase() === "thankyou");

  // Function to render the appropriate widgets based on the active tab
  const renderWidgets = () => {
    switch (activeTab) {
      case 'shop':
        return (
          <div className="sb-widget-row">
            <h3 className="sb-widget-title">Shop Widgets</h3>
            <div className="sb-row">
              {ShopWidgets.map((val, key) => (
                <div className="sb-col-xxl-3 sb-col-lg-4 sb-col-md-6" key={key}>
                  <Widget
                    widget={2}
                    id={val[0]}
                    title={val[1].title}
                    isPro={val[1].is_pro}
                    onChange={handleChange}
                    defaultValue={val[1].is_active}
                    active={isProActive}
                  />
                </div>
              ))}
            </div>
          </div>
        );
      case 'shop-details':
        return (
          <div className="sb-widget-row">
            <h3 className="sb-widget-title">Shop Details Widgets</h3>
            <div className="sb-row">
              {ShopSingleWidgets.map((val, key) => (
                <div className="sb-col-xxl-3 sb-col-lg-4 sb-col-md-6" key={key}>
                  <Widget
                    widget={2}
                    id={val[0]}
                    title={val[1].title}
                    isPro={val[1].is_pro}
                    onChange={handleChange}
                    defaultValue={val[1].is_active}
                    active={isProActive}
                  />
                </div>
              ))}
            </div>
          </div>
        );
      // Add more cases for other widget types (CartWidgets, CheckoutWidgets, etc.)
      case 'cart':
        return (
          <div className="sb-widget-row">
            <h3 className="sb-widget-title">Cart Page Widgets</h3>
            <div className="sb-row">
              {CartWidgets.map((val, key) => (
                <div className="sb-col-xxl-3 sb-col-lg-4 sb-col-md-6" key={key}>
                  <Widget
                    widget={2}
                    id={val[0]}
                    title={val[1].title}
                    isPro={val[1].is_pro}
                    onChange={handleChange}
                    defaultValue={val[1].is_active}
                    active={isProActive}
                  />
                </div>
              ))}
            </div>
          </div>
        );
      case 'checkout':
        return (
            <div className="sb-widget-row">
                <h3 class="sb-widget-title">Checkout Page Widgets</h3>
                <div className="sb-row">
                    {
                        CheckoutWidgets.map( (val, key) => (
                            <div className="sb-col-xxl-3 sb-col-lg-4 sb-col-md-6" key={key}>
                                <Widget 
                                    widget={2} 
                                    id={val[0]}
                                    title={val[1].title}
                                    isPro={val[1].is_pro}
                                    onChange={handleChange}
                                    defaultValue={val[1].is_active}
                                    active={isProActive}
                                />
                            </div>
                        ))
                    }
                    
                </div>
            </div>
        );
        case 'thankyou':
        return (
            <div className="sb-widget-row">
                <h3 class="sb-widget-title">Thankyou Page Widgets</h3>
                <div className="sb-row">
                    {
                        ThankYouWidgets.map( (val, key) => (
                            <div className="sb-col-xxl-3 sb-col-lg-4 sb-col-md-6" key={key}>
                                <Widget 
                                    widget={2} 
                                    id={val[0]}
                                    title={val[1].title}
                                    isPro={val[1].is_pro}
                                    onChange={handleChange}
                                    defaultValue={val[1].is_active}
                                    active={isProActive}
                                />
                            </div>
                        ))
                    }
                    
                </div>
            </div>
        );
        case 'myaccount':
        return (
            <div className="sb-widget-row">
                <h3 class="sb-widget-title">My Account Page Widgets</h3>
                <div className="sb-row">
                    {
                        MyAccountsWidgets.map( (val, key) => (
                            <div className="sb-col-xxl-3 sb-col-lg-4 sb-col-md-6" key={key}>
                                <Widget 
                                    widget={2} 
                                    id={val[0]}
                                    title={val[1].title}
                                    isPro={val[1].is_pro}
                                    onChange={handleChange}
                                    defaultValue={val[1].is_active}
                                    active={isProActive}
                                />
                            </div>
                        ))
                    }
                    
                </div>
            </div>
        );
        case 'hero':
        return (
            <div className="sb-widget-row">
                <h3 class="sb-widget-title">Banner/Hero Widgets</h3>
                <div className="sb-row">
                    {
                        BannerWidgets.map( (val, key) => (
                            <div className="sb-col-xxl-3 sb-col-lg-4 sb-col-md-6" key={key}>
                                <Widget 
                                    widget={2} 
                                    id={val[0]}
                                    title={val[1].title}
                                    isPro={val[1].is_pro}
                                    onChange={handleChange}
                                    defaultValue={val[1].is_active}
                                    active={isProActive}
                                />
                            </div>
                        ))
                    }
                    
                </div>
            </div>
        );
        case 'landing':
        return (
            <div className="sb-widget-row">
                <h3 class="sb-widget-title">Landing Page Widgets</h3>
                <div className="sb-row">
                    {
                        HomeWidgets.map( (val, key) => (
                            <div className="sb-col-xxl-3 sb-col-lg-4 sb-col-md-6" key={key}>
                                <Widget 
                                    widget={2} 
                                    id={val[0]}
                                    title={val[1].title}
                                    isPro={val[1].is_pro}
                                    onChange={handleChange}
                                    defaultValue={val[1].is_active}
                                    active={isProActive}
                                />
                            </div>
                        ))
                    }
                    
                </div>
            </div>
        );
        case 'offer':
        return (
            <div className="sb-widget-row">
                <h3 class="sb-widget-title">Offer Widgets</h3>
                <div className="sb-row">
                    {
                        OfferWidgets.map( (val, key) => (
                            <div className="sb-col-xxl-3 sb-col-lg-4 sb-col-md-6" key={key}>
                                <Widget 
                                    widget={2} 
                                    id={val[0]}
                                    title={val[1].title}
                                    isPro={val[1].is_pro}
                                    onChange={handleChange}
                                    defaultValue={val[1].is_active}
                                    active={isProActive}
                                />
                            </div>
                        ))
                    }
                    
                </div>
            </div>
        );
        
      default:
        return null;
    }
  };

  return (
    <div className="widget-tabs">
      {/* Tab Navigation */}
      <ul className="tab-nav">
        <li
          className={activeTab === 'shop' ? 'active' : ''}
          onClick={() => setActiveTab('shop')}
        >
          Shop
        </li>
        <li
          className={activeTab === 'shop-details' ? 'active' : ''}
          onClick={() => setActiveTab('shop-details')}
        >
          Shop Details
        </li>
        <li
          className={activeTab === 'cart' ? 'active' : ''}
          onClick={() => setActiveTab('cart')}
        >
          Cart
        </li>
        <li
          className={activeTab === 'checkout' ? 'active' : ''}
          onClick={() => setActiveTab('checkout')}
        >
          Checkout
        </li>
        <li
          className={activeTab === 'thankyou' ? 'active' : ''}
          onClick={() => setActiveTab('thankyou')}
        >
          Thankyou
        </li>
        <li
          className={activeTab === 'myaccount' ? 'active' : ''}
          onClick={() => setActiveTab('myaccount')}
        >
          My Account
        </li>
        <li
          className={activeTab === 'hero' ? 'active' : ''}
          onClick={() => setActiveTab('hero')}
        >
          Landing
        </li>
        <li
          className={activeTab === 'offer' ? 'active' : ''}
          onClick={() => setActiveTab('offer')}
        >
          Offer
        </li>
        {/* Add more tabs for other widget categories */}
      </ul>

      {/* Render the Widgets based on the active tab */}
      <div className="tab-content">{renderWidgets()}</div>
    </div>
  );
};

export default WidgetTabs;
