import React from 'react'

function Switch({ 
  id = 'offcanvas', 
  title = 'Enable / Disable', 
  text = 'You can enable / disable sales notification from here.',
  defaultValue = false,
  onChange = () => {}
}) {
  return (
    <div className="sb-offcanvas-option-item sb-d-flex sb-align-items-center sb-justify-content-between sb-flex-wrap">
        <div className="sb-offcanvas-option-content">
            <h4 className="sb-offcanvas-option-title font-semibold">{title}</h4>
            <p>{text}</p>
        </div>
        <div className="sb-offcanvas-option-action">
        <div className="sb-toggle-switch-2">
            <label htmlFor={id}>
                { (defaultValue === true)? <input type="checkbox" className="switch-input" id={id} onChange={onChange} name={id} checked/> : 
                  <input type="checkbox" className="switch-input" id={id} onChange={onChange} name={id}/>
                }
                <span className="sb-toggle-switch-2-text on">on</span>
                <span className="sb-toggle-switch-2-text off">off</span>
                <span className="sb-toggle-switch-2-bg" />
                <span className="sb-toggle-switch-2-handle" />
            </label>
        </div>
        </div>
    </div>
  )
}

export default Switch