import Widget from "./widgets/Widget"
import { Offcanvas, Dropdown, Input, Switch } from "./offcanvas"
import { useState, useEffect } from "@wordpress/element"
import { toast } from 'react-toastify'
import axios from 'axios'
import { Notice } from "@wordpress/components"
import { RawHTML } from "@wordpress/element"
import { __ } from '@wordpress/i18n'

const SaleNotiFication = ({ video, id, docs }) => {
    const hasPro = pure_wc_shopbuild.is_pro_active === undefined ? false : true;
    const proNoticeProduct =  __( 'Use <a target="_blank" href="https://themepure.net/plugins/storebuild/pricing/pricing/">StoreBuild Pro</a> To add more products in notification.', 'shopbuild' );

    const [ saleNotification, setSaleNotification ] = useState(false)
    const [ productListErr, setProductListErr ] = useState(false);
    const [ isManual, setIsManualProducts ] = useState(pure_wc_shopbuild.settings._pure_sale_notification.pure_wc_real_or_manual)
    const [ saleNotificationSettings, setSaleNotificationSettings ]   = useState(pure_wc_shopbuild.settings._pure_sale_notification)
    const [ manualProductsLists, setManualProductsList ]   = useState(pure_wc_shopbuild.settings._pure_sale_notification.pure_wc_manual_products)
    const [ enableSaleNotification, setEnableSaleNotification ]     = useState(pure_wc_shopbuild.settings._pure_sale_notification.pure_wc_enable_sale_notification)

    const handleSaveNotificationSettings = (e) => {
        e.preventDefault()
        const form_data = new FormData()
        form_data.append('action', 'pure_wc_shopbuild_save')
        form_data.append('security', pure_wc_shopbuild.nonce)
        form_data.append('_pure_sale_notification', JSON.stringify(saleNotificationSettings))
        axios.post(pure_wc_shopbuild.ajax_url, form_data)
        .then( res => {
            toast.success(res.data?.data?.message)
        })
        .catch( err => {
            console.log(err)
            toast.error(res.data?.data?.message)
        })
    }

    /**
    * 
    * @param notification setttings 
    */
    const handleNotificationChange = (e) => {
        e.preventDefault()
        if(e.target.value === 'manual'){
            setIsManualProducts('manual')
        }else{
            setIsManualProducts('real')
        }
        setSaleNotificationSettings({...saleNotificationSettings, [e.target.name]:e.target.value})
    }

    useEffect(() => {
        setSaleNotificationSettings({...saleNotificationSettings, ['pure_wc_enable_sale_notification']:enableSaleNotification})
    },[enableSaleNotification])

    const handleManualProductsChange = (val) => {
        const newProductsList = val.length? manualProductsLists.filter( product => product.value != val.value ) : val;
        if(val.length >= 2 && !hasPro){
            setProductListErr(true);
            return;
        }else{
            setManualProductsList( prev => [...prev, ...val])
            setSaleNotificationSettings({...saleNotificationSettings, ['pure_wc_manual_products']:val})
        }
    }

    return (
        <>
            <Widget
                title='Sale Notification'
                description='Your order notification to view your custom for better selling.' 
                toggle={false} 
                settings={true}
                onChange={() => setSaleNotification(saleNotification => !saleNotification)}
                isPro={false}
                active={true}
            />
            <Offcanvas 
                open={saleNotification} 
                onClose={() => setSaleNotification(saleNotification => !saleNotification)} 
                heading='Sale Notification' 
                onSave={handleSaveNotificationSettings}
                video={video}
                id={id}
                docs={docs}
            >
            <div className="sb-offcanvas-option">
                <Switch
                    id='pure_wc_enable_sale_notification'
                    title='Enable / Disable'
                    text='Hide/Show Sale Notification in shop page'
                    defaultValue={enableSaleNotification}
                    onChange={() => setEnableSaleNotification( enableSaleNotification => !enableSaleNotification)}
                />
                <Dropdown
                    id={'pure_wc_notification_position'}
                    title='Notification Position'
                    text='Select a position.'
                    data={[
                        { value: 'bottom_left', label: 'Bottom Left'},
                        { value: 'bottom_right', label: 'Bottom Right'},
                        { value: 'top_right', label: 'Top Right'},
                        { value: 'top_left', label: 'Top Left'},
                    ]}
                    selected={saleNotificationSettings.pure_wc_notification_position}
                    onChange={handleNotificationChange}
                />
                <Dropdown
                    id='pure_wc_real_or_manual'
                    title='Notification type'
                    text='Select a type.'
                    data={[
                        { value: 'real', label: 'Real'},
                        { value: 'manual', label: 'Manual'}
                    ]}
                    selected={saleNotificationSettings.pure_wc_real_or_manual}
                    onChange={handleNotificationChange}
                />
                
                {(isManual == 'manual') && <div class="sb-offcanvas-section-title">
                    <h3>Notification products</h3>
                </div>}
                {(isManual == 'manual') && <Dropdown
                    id='pure_wc_manual_products'
                    title='Products Lists'
                    text='Choose multiple products for notification.'
                    data={pure_wc_shopbuild.pure_products}
                    selected={manualProductsLists}
                    isMulti={true}
                    onChange={handleManualProductsChange}
                    isOptionDisabled={ () => productListErr }
                />}
                {productListErr && <Notice status="warning" isDismissible={false}>
                                <RawHTML>{proNoticeProduct}</RawHTML>
                            </Notice>}
                {(isManual == 'manual') &&<div class="sb-offcanvas-section-title">
                    <h3>Fake buyer info</h3>
                </div>}
                {(isManual == 'manual') && <Input
                    id='pure_wc_fname_of_buyer'
                    title='Name of a buyer'
                    text='Salim Rana'
                    defaultValue={saleNotificationSettings.pure_wc_fname_of_buyer}
                    type='text'
                    onChange={handleNotificationChange}
                />}
                {(isManual == 'manual') && <Input
                    id='pure_wc_lname_of_buyer'
                    title='Name of a buyer'
                    text='Salim Rana'
                    defaultValue={saleNotificationSettings.pure_wc_lname_of_buyer}
                    type='text'
                    onChange={handleNotificationChange}
                />}
                {(isManual == 'manual') && <Input
                    id='pure_wc_city_of_buyer'
                    title='City'
                    text='Mirpur'
                    defaultValue={saleNotificationSettings.pure_wc_city_of_buyer}
                    type='text'
                    onChange={handleNotificationChange}
                />}
                {(isManual == 'manual') && <Input
                    id='pure_wc_state_of_buyer'
                    title='State'
                    text='Dhaka'
                    defaultValue={saleNotificationSettings.pure_wc_state_of_buyer}
                    type='text'
                    onChange={handleNotificationChange}
                />}
                {(isManual == 'manual') && <Input
                    id='pure_wc_country_of_buyer'
                    title='Country'
                    text='Bangladesh'
                    defaultValue={saleNotificationSettings.pure_wc_country_of_buyer}
                    type='text'
                    onChange={handleNotificationChange}
                />}
            </div>
            <div className="sb-offcanvas-option">
                <Input
                    id='pure_wc_number_of_products'
                    title='Products Limit'
                    text='Number of products to show in notification.'
                    defaultValue={saleNotificationSettings.pure_wc_number_of_products}
                    type='number'
                    onChange={handleNotificationChange}
                />
                <Dropdown
                    id='pure_wc_how_old_product'
                    title='How Old Product?'
                    text='Select a time.'
                    data={[
                        { value: '1 day', label: '1 Day'},
                        { value: '3 days', label: '3 Days'},
                        { value: '5 days', label: '5 Days'},
                        { value: '1 week', label: '1 Week'},
                        { value: '2 week', label: '2 Weeks'},
                        { value: '1 month', label: '1 Month'},
                    ]}
                    selected={saleNotificationSettings.pure_wc_how_old_product}
                    onChange={handleNotificationChange}
                />
                <Input
                    id='pure_wc_first_load_time'
                    title='First Loading Time'
                    text='After how long it will appear on page load.(Time in seconds)'
                    defaultValue={saleNotificationSettings.pure_wc_first_load_time}
                    type='number'
                    onChange={handleNotificationChange}
                />
                <Input
                    id='pure_wc_notification_showtime'
                    title='Notification Duration'
                    text='How long you want to display.(Time in seconds)'
                    defaultValue={saleNotificationSettings.pure_wc_notification_showtime}
                    type='number'
                    onChange={handleNotificationChange}
                />
                <Input
                    id='pure_wc_notification_interval'
                    title='Notification Interval'
                    text='How long you want to hold for a notification to show.(Time in seconds)'
                    defaultValue={saleNotificationSettings.pure_wc_notification_interval}
                    type='number'
                    onChange={handleNotificationChange}
                />
            </div>
            </Offcanvas>
        </>
    )
}

export default SaleNotiFication;