import React from 'react'

function Input({ 
    title = 'Input Label', 
    text = 'You can handle the sales notification width.', 
    defaultValue = 'Hello World!', 
    type = 'text',
    id = '',
    onChange = () => {}
}) {
  return (
    <div className="sb-offcanvas-option-item sb-d-flex sb-align-items-center sb-justify-content-between sb-flex-wrap">
        <div className="sb-offcanvas-option-content">
            <h4 className="sb-offcanvas-option-title">{title}</h4>
            <p>{text}</p>
        </div>
        <div className="sb-offcanvas-option-action">
            <div className="sb-offcanvas-input">
                {type != 'textarea' && <input type={type} defaultValue={defaultValue} id={id} name={id} onChange={onChange}/>}
                {type == 'textarea' && <textarea id={id} name={id} onChange={onChange}>{defaultValue}</textarea>}
            </div>
        </div>
    </div>
  )
}

export default Input