import React from 'react'
import TabHead from '../Sections/TabHead'
import Widget from '../widgets/Widget'
import { toast } from 'react-toastify'
import axios from 'axios'
import { useState, useEffect } from '@wordpress/element'
import CurrencySwitcher from '../CurrencySwitcher'
import SaleNotiFication from '../SaleNotification'
import VariationSwatch from '../VariationSwatch'
import Invoice from '../Invoice'
import Quickview from '../Quickview'
import Backorder from '../Backorder'
import PreOrder from '../PreOrder'
import QuickCheckout from '../QuickCheckout'

function Modules() {
    const [ wishlistToggle, setWishlistToggle ] = useState(pure_wc_shopbuild.settings._pure_shopbuild_modules.pure_wc_wishlist)
    const [ quickviewToggle, setQuickviewToggle ] = useState(pure_wc_shopbuild.settings._pure_shopbuild_modules.pure_wc_quickview)
    const [ compareToggle, setCompareToggle ] = useState(pure_wc_shopbuild.settings._pure_shopbuild_modules.pure_wc_compare)
    const [ addToCartNotification, setAddToCartNotification ] = useState(pure_wc_shopbuild.settings._pure_shopbuild_modules.pure_wc_add_to_cart_notification)
    const [ saleBadge, setSaleBadge ] = useState(pure_wc_shopbuild.settings._pure_shopbuild_modules.pure_wc_sale_badge)
    const [ crossSell, setCrossSell ] = useState(pure_wc_shopbuild.settings._pure_shopbuild_modules.pure_wc_cross_sell)
    const [ upSell, setUpSell ] = useState(pure_wc_shopbuild.settings._pure_shopbuild_modules.pure_wc_up_sell)
    const [ flashSaleCountDown, setFlashSaleCountdown ] = useState(pure_wc_shopbuild.settings._pure_shopbuild_modules.pure_wc_flash_sale_countdown)
    const [ general, setGeneral ]   = useState(pure_wc_shopbuild.pure_swatches_settings?.tpwvs_general)
    const [ shop, setShop ]  = useState(pure_wc_shopbuild.pure_swatches_settings?.tpwvs_shop)
    const [ modules, setModules ]   = useState(pure_wc_shopbuild.settings._pure_shopbuild_modules)
    const [ proModules, setProModules ]   = useState(pure_wc_shopbuild.pro_modules)

    useEffect(() =>{
        setModules({...modules, pure_wc_wishlist:wishlistToggle})
    },[wishlistToggle])

    useEffect(() =>{
        setModules({...modules, pure_wc_quickview:quickviewToggle})
    },[quickviewToggle])

    useEffect(() =>{
        setModules({...modules, pure_wc_compare:compareToggle})
    },[compareToggle])

    useEffect(() =>{
        setModules({...modules, pure_wc_add_to_cart_notification:addToCartNotification})
    },[addToCartNotification])

    useEffect(() =>{
        setModules({...modules, pure_wc_sale_badge:saleBadge})
    },[saleBadge])

    useEffect(() =>{
        setModules({...modules, pure_wc_cross_sell:crossSell})
    },[crossSell])

    useEffect(() =>{
        setModules({...modules, pure_wc_up_sell:upSell})
    },[upSell])

    useEffect(() =>{
        setModules({...modules, pure_wc_flash_sale_countdown:flashSaleCountDown})
    },[flashSaleCountDown])

    const handleSaveSettings = (e) =>{
        e.preventDefault()
        const form_data = new FormData()
        form_data.append('action', 'pure_wc_shopbuild_save')
        form_data.append('security', pure_wc_shopbuild.nonce)
        form_data.append('_pure_shopbuild_modules', JSON.stringify(modules))
        axios.post(pure_wc_shopbuild.ajax_url, form_data)
        .then( res => {
            if( res.data?.success === false ){
                toast.error(res.data?.data?.message)
                return;
            }
            toast.success(res.data?.data?.message)
        })
        .catch( err => {
            console.log(err)
            toast.error(res.data?.data?.message)
        })
    }
    
    return (
        <>
            {/* modules content tab */}
            <div className="sb-features-tab-content">
                {/* Controls Top setting all */}
                <div className="sb-controls-top">
                    <TabHead 
                        title='Disable/Enable Modules' 
                        text='You can enable or disable all options by one click.'
                        onSave={handleSaveSettings}/>
                </div>
                <div className="sb-widget-wrapper">
                <div className="sb-widget-container">
                    <div className="sb-widget-row">
                        <h3 class="sb-widget-title">WooCommerce Modules</h3>
                        <div className="sb-row">
                            <div className="sb-col-xxl-3 sb-col-lg-4 sb-col-md-6">
                                <VariationSwatch 
                                    video="https://www.youtube.com/embed/gTZdwCySYrM?si=csxG79L2VBeY6_sQ"
                                    id='pure_wc_variation_swatches'
                                    docs='https://themepure.net/plugins/woocommerce-variation-swatches/docs/'
                                />
                            </div>
                            <div className="sb-col-xxl-3 sb-col-lg-4 sb-col-md-6">
                                <SaleNotiFication
                                    video="https://www.youtube.com/embed/ndW5X4lh80c?si=LQzqLgYIlKTvkDJO"
                                    id='pure_wc_sale_notification'
                                    docs="https://themepure.net/plugins/storebuild/docs/sale-notification/"
                                />
                            </div>
                            <div className="sb-col-xxl-3 sb-col-lg-4 sb-col-md-6">
                                <CurrencySwitcher
                                    video="https://www.youtube.com/embed/jKrW7LaH3uw?si=sO9jzw9MabDa4BsT"
                                    id='pure_wc_currency_switcher'
                                    docs="https://themepure.net/plugins/storebuild/docs/currency-switcher/"
                                />
                            </div>
                            <div className="sb-col-xxl-3 sb-col-lg-4 sb-col-md-6">
                                <Invoice
                                    video="https://www.youtube.com/embed/eFEAxL0xbA4?si=JiJDuIFbs7DJBQwr"
                                    id='pure_wc_invoice'
                                    docs="https://themepure.net/plugins/storebuild/docs/invoice/"
                                />
                            </div>
                            <div className="sb-col-xxl-3 sb-col-lg-4 sb-col-md-6">
                                <Widget
                                    id='pure_wc_wishlist' 
                                    description='User can keep products in wishlist.' 
                                    title='Wishlist' 
                                    toggle={true} 
                                    settings={false}
                                    defaultValue={wishlistToggle}
                                    onChange={() => setWishlistToggle(wishlistToggle => !wishlistToggle)}
                                    isPro={false}
                                    active={true}
                                />
                            </div>
                            <div className="sb-col-xxl-3 sb-col-lg-4 sb-col-md-6">
                                <Quickview
                                    video="https://www.youtube.com/embed/pNxfuvBhMwk?si=1u38PjdY9cGXMUDX"
                                    id='pure_wc_quickview'
                                    docs="https://themepure.net/plugins/storebuild/docs/quickview/"
                                />
                            </div>
                            <div className="sb-col-xxl-3 sb-col-lg-4 sb-col-md-6">
                                <Widget 
                                    id='pure_wc_compare'
                                    title="Compare" 
                                    description='Compare products for a better selling experience.' 
                                    settings={false} 
                                    toggle={true}
                                    defaultValue={compareToggle}
                                    onChange={() => setCompareToggle(compareToggle => !compareToggle)}
                                    isPro={false}
                                    active={true}
                                />
                            </div>
                            <div className="sb-col-xxl-3 sb-col-lg-4 sb-col-md-6">
                                <Widget 
                                    id='pure_wc_add_to_cart_notification'
                                    title="Real-Time Cart Notification" 
                                    description='Notify user when a product is added to cart.' 
                                    settings={false} 
                                    toggle={true}
                                    defaultValue={addToCartNotification}
                                    onChange={() => setAddToCartNotification(addToCartNotification => !addToCartNotification)}
                                    isPro={false}
                                    active={true}
                                />
                            </div>
                            <div className="sb-col-xxl-3 sb-col-lg-4 sb-col-md-6">
                                <Widget 
                                    id='pure_wc_sale_badge'
                                    title="Sale Badge" 
                                    description='Show sale badge on product.' 
                                    settings={false} 
                                    toggle={true}
                                    isPro={false}
                                    defaultValue={saleBadge}
                                    active={true}
                                    onChange={() => setSaleBadge(saleBadge => !saleBadge)}
                                />
                            </div>
                            <div className="sb-col-xxl-3 sb-col-lg-4 sb-col-md-6">
                                <Widget 
                                    id='pure_wc_sale_countdown'
                                    title="Flash Sale Countdown" 
                                    description='Show sale countdown on product.' 
                                    settings={false} 
                                    toggle={true}
                                    isPro={false}
                                    active={true}
                                    defaultValue={flashSaleCountDown}
                                    onChange={() => setFlashSaleCountdown(flashSaleCountDown => !flashSaleCountDown)}
                                />
                            </div>
                            <div className="sb-col-xxl-3 sb-col-lg-4 sb-col-md-6">
                                <Widget 
                                    id='pure_wc_cross_sell_popup'
                                    title="Cart Cross Sell Popup" 
                                    description='Show cross sell products on the fly when adding to cart. This will only work with woocommerce classic layout.' 
                                    settings={false} 
                                    toggle={true}
                                    isPro={true}
                                    active={proModules?.pure_wc_cross_sell || false}
                                    defaultValue={crossSell}
                                    onChange={() => setCrossSell(crossSell => !crossSell)}
                                />
                            </div>
                            {/* <div className="sb-col-xxl-3 sb-col-lg-4 sb-col-md-6">
                                <Widget 
                                    id='pure_wc_up_sell_popup'
                                    title="Cart Up Sell" 
                                    description='Show up sell products on cart page.' 
                                    settings={false} 
                                    toggle={true}
                                    isPro={false}
                                    active={true}
                                    defaultValue={upSell}
                                    onChange={() => setUpSell(upSell => !upSell)}
                                />
                            </div> */}
                            <div className="sb-col-xxl-3 sb-col-lg-4 sb-col-md-6">
                                <Backorder
                                    video="https://www.youtube.com/embed/IOsCyW09K5k?si=qtZGOSM7EHzrSl2C"
                                    id='pure_wc_backorder'
                                    docs="https://themepure.net/plugins/storebuild/docs/backorder/"
                                />
                            </div>
                            <div className="sb-col-xxl-3 sb-col-lg-4 sb-col-md-6">
                                <PreOrder
                                    video="https://www.youtube.com/embed/hLgLRPgRXdc?si=PwjnXKSaAq0d22Rm"
                                    id='pure_wc_preorder'
                                    docs="https://themepure.net/plugins/storebuild/docs/pre-order/"
                                />
                            </div>
                            <div className="sb-col-xxl-3 sb-col-lg-4 sb-col-md-6">
                                <QuickCheckout
                                    video="https://www.youtube.com/embed/k5JxdTGzWS4?si=DDsxWCZI_ANKWyhU"
                                    id='pure_wc_quick_checkout'
                                    docs="https://themepure.net/plugins/storebuild/docs/quick-checkout/"
                                />
                            </div>
                        </div>
                    </div>
                </div>
                <div className="sb-widget-option-save sb-d-flex sb-justify-content-end">
                    <a href="#" className="sb-btn-fill" onClick={handleSaveSettings}>Save Settings</a>
                </div>
                </div>
            </div>
        </>
    )
}

export default Modules