import React from 'react'
import Select from 'react-select';

function Dropdown({ 
    id = '_dropdown', 
    data = [], 
    title = 'Dropdown', 
    text = 'Some long text will go here.',
    onChange = () => {},
    selected = '',
    isMulti = false,
    isOptionDisabled = () => { return false }
}) {
    const selectEl = React.useRef()

    React.useEffect(() => {
        new Choices(selectEl.current)
    },[])
    return (
        <div className="sb-offcanvas-option-item sb-d-flex sb-align-items-center sb-justify-content-between sb-flex-wrap">
            <div className="sb-offcanvas-option-content">
                <h4 className="sb-offcanvas-option-title">{title}</h4>
                <p>{text}</p>
            </div>
            <div className="sb-offcanvas-option-action">
                <div className="sb-offcanvas-select sb-position-select sb-settings-option-select sb-common-select">
                    {isMulti? <Select
                        defaultValue={selected}
                        isMulti
                        isClearable
                        name="manual_products"
                        options={data}
                        onChange={onChange}
                        className="basic-multi-select"
                        classNamePrefix="select"
                        isOptionDisabled={isOptionDisabled}
                    /> : 
                    <select ref={selectEl} name={id} onChange={onChange}>
                        { data.map( val => (
                            <option value={val.value} selected={ (val.value === selected)? true : false }>{val.label}</option>
                        )) }
                    </select>
                    }
                </div>
            </div>
        </div>
    )
}

export default Dropdown