import React from 'react'
import playIcon from '../../assets/img/icon/play.svg'

function VideoCard({
    cols = 'sb-col-lg-4',
    img_src = '', 
    title = 'Go somewhere',
    link = '#',
    onPopup
}) {

    const handleClick = (e) =>  {
        e.preventDefault();
        onPopup(link);
    }

    return (
        <div className={cols}>
            <div className="sb-tutorial-item">
                <div className="sb-tutorial-thumb">
                    <img src={img_src} alt=""/>
                    <div className="sb-tutorial-play">
                        <a href={link} className="sb-tutorial-play-btn" target='_blank' onClick={handleClick}>
                            <img src={playIcon} alt=""/>
                        </a>
                    </div>
                </div>
                <div className="sb-tutorial-content">
                    <h4 className="sb-tutorial-title">{title}</h4>
                </div>
            </div>
        </div>
    )
}

export default VideoCard