import { Offcanvas, Switch } from "./offcanvas";
import { Panel, PanelBody, PanelRow, TextControl, Button, __experimentalNumberControl as NumberControl, Icon } from '@wordpress/components';
import { useState, useEffect } from '@wordpress/element';
import { useDispatch } from '@wordpress/data';
import { store as noticesStore } from '@wordpress/notices';
import { __ } from '@wordpress/i18n';
import { RawHTML } from "@wordpress/element";
import Widget from "./widgets/Widget";
import apiFetch from '@wordpress/api-fetch';
import { Notice } from '@wordpress/components';

export default function CurrencySwitcher({video, id, docs}){
    const { createSuccessNotice, createErrorNotice } = useDispatch( noticesStore );
    const [ currency, setCurrency ] = useState({
        currency: '',
        symbol: '',
        rate: 0,
        position: '',
        display: false
    });
    const [ currencySwitcher, setCurrencySwitcher ] = useState(false);
    const [ currencySwitcherSettings, setCurrencySwitcherSettings ]   = useState(pure_wc_shopbuild.settings._pure_currency_switcher.pure_wc_currency_switcher_list);
    const [ currencySwitcherEnable, setCurrencySwitcherEnable ]   = useState(pure_wc_shopbuild.settings._pure_currency_switcher.pure_wc_enable_module);
    const defaultCurrency = pure_wc_shopbuild.default_currency;

    const hasPro = pure_wc_shopbuild.is_pro_active === undefined ? false : true;

    const saveSettings = () => {
        apiFetch( {
            path: '/wp/v2/settings',
            method: 'POST',
            data: {
                _pure_currency_switcher: {
                    pure_wc_enable_module: currencySwitcherEnable,
                    pure_wc_currency_switcher_list: currencySwitcherSettings
                },
            },
        }).then(() => {
            setCurrencySwitcher(currencySwitcher => !currencySwitcher);
            createSuccessNotice(
                __( 'Settings saved.', 'shopbuild' )
            );
        }).catch(() => {
            createErrorNotice(
                __( 'Failed to save settings.', 'shopbuild' )
            );
        });
    };

    const addCurrencyToList = () => {
        if(currency.currency === '' || currency.symbol === '' || currency.rate === ''){
            createErrorNotice(
                __( 'Please fillout all fields.', 'shopbuild' )
            );
            return;
        }
        setCurrencySwitcherSettings([...currencySwitcherSettings, currency]);
        setCurrency({
            currency: '',
            symbol: '',
            rate: 0.009,
            position: '',
            display: false
        });
    }


    const removeCurrencyFromList = (currency) => {
        const updatedSettings = currencySwitcherSettings.filter(item => item.currency !== currency);
        setCurrencySwitcherSettings(updatedSettings);
    }

    const updateCurrencySettings = (currency, value, prop) => {
        // Create a new array with updated object
        const updatedSettings = currencySwitcherSettings.map(item => {
            if (item.currency === currency) {
                // Return a new object with updated name
                return { ...item, [prop]: value};
            }
            return item;
        });
        // Update state with the new array
        setCurrencySwitcherSettings(updatedSettings);
    };


    const proNotice =  __( 'Use <a target="_blank" href="https://themepure.net/plugins/storebuild/pricing/pricing/">StoreBuild Pro</a> To add more currency in the list.', 'shopbuild' );
    

    return (
        <>
            <Widget
                title='Currency Switcher'
                description='User can switch currency for better selling experience.' 
                toggle={false} 
                settings={true}
                onChange={() => setCurrencySwitcher(currencySwitcher => !currencySwitcher)}
                isPro={false}
                active={true}
            />
            <Offcanvas 
                open={currencySwitcher} 
                onClose={() => setCurrencySwitcher(currencySwitcher => !currencySwitcher)} 
                heading='Currency Switcher' 
                onSave={saveSettings}
                video={video}
                id={id}
                docs={docs}
            >
            <div className="sb-offcanvas-option">
                <Switch
                    id='pure_wc_enable_module'
                    title='Enable / Disable'
                    text='Hide/Show Currency Switcher Module'
                    defaultValue={currencySwitcherEnable}
                    onChange={() => setCurrencySwitcherEnable( currencySwitcherEnable => !currencySwitcherEnable)}
                />
                <Panel header='Currency List'>
                    <PanelBody title={`Default Currency (${defaultCurrency.currency})`} initialOpen={true}>
                        <PanelRow>
                            <p><strong>{__('Currency Name:', 'shopbuild')} </strong> <b>{defaultCurrency.currency}</b></p>
                            <p><strong>{__('Currency Symbol:', 'shopbuild')} </strong> <b><RawHTML>{defaultCurrency.symbol}</RawHTML></b></p>
                        </PanelRow>
                    </PanelBody>
                    {currencySwitcherSettings.map((currency, index) => {
                    if( !hasPro && index >= 2 ) return;
                    return (<PanelBody title={currency.currency} initialOpen={false} key={index}>
                        <PanelRow className="sb-flex-wrap">
                            <TextControl
                                label="Currency Name"
                                value={ currency.currency }
                                onChange={ ( value ) => updateCurrencySettings( currency.currency, value, 'currency' ) }
                                className="mb-10"
                                placeholder="USD"
                            />
                            <TextControl
                                label="Currency Symbol"
                                value={ currency.symbol }
                                onChange={ ( value ) => updateCurrencySettings( currency.currency, value, 'symbol' ) }
                                className="mb-10"
                                placeholder="$"
                            />
                            <NumberControl
                                label="Currency Exchange Rate(From default currency)"
                                value={ currency.rate }
                                onChange={ ( value ) => updateCurrencySettings( currency.currency, value, 'rate' ) }
                                className="mb-10"
                                placeholder="0.090"
                            />
                        </PanelRow>
                        <Button 
                            isDestructive 
                            variant="secondary" 
                            size="small" 
                            onClick={() => removeCurrencyFromList(currency.currency) }
                            icon={<Icon icon="trash" />}
                            iconSize={15}
                        >Remove This Currency</Button>
                    </PanelBody>)
                    }
                    )}
                </Panel>

                <Panel header="Add Your New Currency">
                { !hasPro && <Notice status="warning" isDismissible={false}>
                                <RawHTML>{proNotice}</RawHTML>
                            </Notice> }
                { hasPro && <PanelBody title={'Adding New'} initialOpen={true}>
                        <PanelRow className="sb-flex-wrap">
                            
                             <TextControl
                                label="Currency Name"
                                value={ currency.currency }
                                onChange={ ( value ) => setCurrency( (prev) => ({ ...prev, currency: value }) ) }
                                className="mb-10"
                                placeholder="USD"
                            />
                            <TextControl
                                label="Currency Symbol"
                                value={ currency.symbol }
                                onChange={ ( value ) => setCurrency( (prev) => ({ ...prev, symbol: value }) ) }
                                className="mb-10"
                                placeholder="$"
                            />
                            <NumberControl
                                label="Currency Exchange Rate(From default currency)"
                                onChange={ ( value ) => setCurrency( (prev) => ({ ...prev, rate: value }) ) }
                                value={ currency.rate }
                                placeholder="0.090"
                                className="mb-10"
                            />
                            <Button isPrimary variant="primary" size="small" onClick={addCurrencyToList}>Add This Currency</Button>
                        </PanelRow>
                    </PanelBody>}
                </Panel>
            </div>
            </Offcanvas>
        </>
    )
}