import { Offcanvas, Switch, Input } from "./offcanvas";
import { useState, useEffect } from '@wordpress/element';
import { useDispatch } from '@wordpress/data';
import { store as noticesStore } from '@wordpress/notices';
import { __ } from '@wordpress/i18n';
import Widget from "./widgets/Widget";
import apiFetch from '@wordpress/api-fetch';

export default function QuickCheckout({video, id, docs}){
    const { createSuccessNotice, createErrorNotice } = useDispatch( noticesStore );
    const [ enable, setEnable ] = useState(false)
    const [ settings, setSettings ]   = useState(pure_wc_shopbuild.settings._pure_quick_checkout_settings);
    const [ modules, setModules ] = useState(pure_wc_shopbuild.pro_modules);

    const saveSettings = () => {
        apiFetch( {
            path: '/wp/v2/settings',
            method: 'POST',
            data: {
                _pure_quick_checkout_settings: settings,
            },
        }).then(() => {
            createSuccessNotice(
                __( 'Settings saved.', 'shopbuild' )
            );
            setEnable(enable => !enable);
        }).catch(() => {
            createErrorNotice(
                __( 'Failed to save settings.', 'shopbuild' )
            );
            setEnable(enable => !enable);
        });
    };

    return (
        <>
            <Widget
                title='Quick Checkout'
                description='Enable quickcheck for your customers. Checkout will be faster from your shop page and product page.' 
                toggle={false} 
                settings={true}
                onChange={() => setEnable(enable => !enable)}
                isPro={true}
                active={modules?.pure_wc_quickcheckout || false}
            />
            <Offcanvas 
                open={enable} 
                onClose={() => setEnable(enable => !enable)} 
                heading='Quick Checkout Settings' 
                onSave={saveSettings}
                video={video}
                id={id}
                docs={docs}
            >
            <div className="sb-offcanvas-option">
                <Switch
                    id='pure_wc_enable_quickcheckout_module'
                    title='Enable / Disable'
                    text='Activate/Deactivate Quick Checkout System.'
                    defaultValue={settings.enable}
                    onChange={() => setSettings( prev => ({ ...prev, enable: !settings.enable }) )}
                />
            </div>
            <div class="sb-offcanvas-section-title">
                <h3>Show In Shop Page</h3>
            </div>
            <div className="sb-offcanvas-option">
                <Switch
                    id='pure_wc_enable_quickcheckcout_in_shop'
                    title='Show in shop?'
                    text=''
                    defaultValue={settings.show_in_archive}
                    onChange={() => setSettings( prev => ({ ...prev, show_in_archive: !settings.show_in_archive }) )}
                />
            </div>
            <div class="sb-offcanvas-section-title">
                <h3>Button's Settings</h3>
            </div>
            <div className="sb-offcanvas-option">
                <Input
                    id='pure_wc_quickcheckout_button_text'
                    title='Button Text'
                    text='Change the text of the Buy Now button.'
                    defaultValue={settings.button_text}
                    type='text'
                    onChange={(e) => setSettings( prev => ({ ...prev, button_text: e.target.value }) )}
                />
            </div>
            </Offcanvas>
        </>
    )
}