import { Offcanvas, Switch, Dropdown } from "./offcanvas";
import { useState, useEffect } from '@wordpress/element';
import { useDispatch } from '@wordpress/data';
import { store as noticesStore } from '@wordpress/notices';
import { __ } from '@wordpress/i18n';
import { RawHTML } from "@wordpress/element";
import Widget from "./widgets/Widget";
import apiFetch from '@wordpress/api-fetch';
import MediaUploader from "./media/MediaUploader";
import { Notice } from '@wordpress/components';

export default function Quickview({ video, id, docs }){
    const { createSuccessNotice, createErrorNotice } = useDispatch( noticesStore );
    const [ enable, setEnable ] = useState(false)
    const [ settings, setSettings ]   = useState(pure_wc_shopbuild.settings._pure_quickview_settings);
    const templates  = pure_wc_shopbuild.quickview_templates;

    const saveSettings = () => {
        apiFetch( {
            path: '/wp/v2/settings',
            method: 'POST',
            data: {
                _pure_quickview_settings: settings,
            },
        }).then(() => {
            createSuccessNotice(
                __( 'Settings saved.', 'shopbuild' )
            );
            setEnable(enable => !enable);
        }).catch(() => {
            createErrorNotice(
                __( 'Failed to save settings.', 'shopbuild' )
            );
            setEnable(enable => !enable);
        });
    };

    const hasPro = pure_wc_shopbuild.is_pro_active === undefined ? false : true;
    const proNotice =  __( 'Use <a target="_blank" href="https://themepure.net/plugins/storebuild/pricing/pricing/">StoreBuild Pro</a> To add custom elementor templates.', 'shopbuild' );

    return (
        <>
            <Widget
                title='Quickview'
                description='User can switch currency for better selling experience.' 
                toggle={false} 
                settings={true}
                onChange={() => setEnable(enable => !enable)}
                isPro={false}
                active={true}
            />
            <Offcanvas 
                open={enable} 
                onClose={() => setEnable(enable => !enable)} 
                heading='Quickview Settings' 
                onSave={saveSettings}
                video={video}
                id={id}
                docs={docs}
            >
            <div className="sb-offcanvas-option">
                <Switch
                    id='pure_wc_enable_quickview_module'
                    title='Enable / Disable'
                    text='Deactivate/Activate Quickview System.'
                    defaultValue={settings.enable}
                    onChange={() => setSettings( prev => ({ ...prev, enable: !settings.enable }) )}
                />
                <Dropdown
                    id='pure_wc_quickview_template_type'
                    title='Template type'
                    text='Select a type.'
                    data={[
                        { value: 'default', label: 'Default' },
                        { value: 'custom', label: 'Custom' }
                    ]}
                    selected={settings.template_type}
                    onChange={(e) => setSettings(prev => ({ ...prev, template_type: e.target.value }))}
                />
                {(settings.template_type === 'custom' && !hasPro) && (
                    <Notice status="warning" isDismissible={false}>
                        <RawHTML>{proNotice}</RawHTML>
                    </Notice>
                )}
                {(settings.template_type === 'custom' && hasPro) && (
                    <Dropdown
                        id='pure_wc_quickview_template'
                        title='Templates'
                        text='Select a template.'
                        data={templates}
                        selected={settings.template}
                        onChange={(e) => setSettings(prev => ({ ...prev, template: e.target.value }))}
                    />
                )}
            </div>
            </Offcanvas>
        </>
    )
}