import React from 'react'

function TabHead({ 
    title = 'Heading', 
    text = 'Some text will go here', 
    onSave = (e) => {e.preventDefault()}, 
    onToggle = (e) => {e.preventDefault()}, 
    toggle = false,
    toggleActive = false }) {

    return (
        <div className="sb-controls-wrapper">
            <div className="sb-row sb-align-items-center">
            <div className="sb-col-xl-5 sb-col-lg-6">
                <div className="sb-controls-content">
                <h4 className="sb-controls-title">{title}</h4>
                <p>{text}</p>
                </div>
            </div>
            <div className="sb-col-xl-7 sb-col-lg-6">
                <div className="sb-controls-setting sb-d-md-flex sb-justify-content-lg-end">
                    { toggle && <div className={toggleActive? "active sb-toggle-switch sb-d-flex sb-align-items-center" : "sb-toggle-switch sb-d-flex sb-align-items-center"}>
                        <span>Disable All</span>
                        <label htmlFor='toggle'/>
                        <input type="checkbox" className="sb-toggle-switch-input" id='toggle' onChange={onToggle}/>
                        <span>Enable All</span>
                    </div> }
                    <div className="sb-controls-save ">
                        <a href="javascript:void(0)" className="sb-btn-fill" onClick={onSave}>Save Settings</a>
                    </div>
                </div>
            </div>
            </div>
        </div>
    )
}

export default TabHead