import React, { useState, useEffect } from 'react'
import Button from '../button/Button'
import {Welcome, Home, Dymond, Setting} from '../icons'
import logo from '../../assets/img/logo.png'
import './navs.scss'

function Navs({ navTo, hash }) {
  const [navs, setNavs] = useState(null);

  useEffect(() => {
    setNavs([
      { hash: '#welcome', title: 'Welcome', icon: <Welcome/> },
      { hash: '#woocommerce', title: 'WooCommerce', icon: <Home/> },
      { hash: '#widgets', title: 'Widgets', icon: <Dymond/> },
      { hash: '#modules', title: 'Modules', icon: <Setting/> },
    ])
  }, [navs]);
  
  return (
    <>
        {/* <!-- top tab area --> */}
        <div className='sb-d-flex sb-top-nav'>
          <div className='sb-logo'>
            <img src={logo} alt='logo'/>
          </div>
          <div class="sb-panel-tab sb-d-md-inline-block">
              <div class="sb-panel-tab-inner sb-d-flex sb-flex-wrap" >
                  { navs && navs.map(nav => (
                    <a href={nav.hash} onClick={navTo}>
                      <Button 
                        activeClass={ (hash === nav.hash)? 'active' : ''} 
                        btnText={nav.title}
                        icon={nav.icon}
                      />
                    </a>
                  )) }
              </div>
          </div>
        </div>
    </>
  )
}

export default Navs