import React from 'react'

function Radio({ 
    title = 'Radio', 
    text = 'Select Content Type', 
    input1 = {
        _name: 'radio',
        _value: 'radio 1',
        _label: 'Radio 1',
    },
    input2 = {
        _name: 'radio',
        _value: 'radio 2',
        _label: 'Radio 2',
    },
    onChange = () => {}
}) {
  return (
    <div className="sb-offcanvas-option-item sb-d-flex sb-align-items-center sb-justify-content-between sb-flex-wrap">
        <div className="sb-offcanvas-option-content">
            <h4 className="sb-offcanvas-option-title">{title}</h4>
            <p>{text}</p>
        </div>
        <div className="sb-offcanvas-option-action">
        <div className="sb-offcanvas-radio-wrapper sb-d-flex sb-align-items-center">
            <div className="sb-radio-toggle">
            <label htmlFor={input1._name}>
                <input 
                    type="checkbox" 
                    id={input1._name} 
                    name={input1._name} 
                    value={input1._value}
                    onChange={onChange}
                />
                <span className="sb-radio-handle">{input1._label}</span>
            </label>
            </div>
            <div className="sb-radio-toggle">
            <label htmlFor={input2._name + "On"}>
                <input 
                    type="checkbox" 
                    id={input2._name+"On"} 
                    name={input2._name}
                    value={input2._value}
                    onChange={onChange}
                />
                <span className="sb-radio-handle">{input2._label}</span>
            </label>
            </div>
        </div>
        </div>
    </div>
  )
}

export default Radio