import React from "react";
import { Welcome, Modules, Woocommerce, Elements, Style } from "./components/tabs";
import Navs from "./components/navs/Navs";
import { ToastContainer } from "react-toastify";
import Notices from "./components/Notices";
import { useState } from "@wordpress/element";

/**
 * Import the stylesheet for the plugin.
 */
import './assets/css/choices.css';
import './assets/scss/core.scss';
import 'react-toastify/dist/ReactToastify.css';

/**
 * Import Js Files
 */
import './assets/js/alpine';
import './assets/js/choices';


const App = () => {
  
  const [stateHash, setHash] = useState(window.location.hash)
  /** navigation */
  const navTo = (e) =>{
    window.location.hash = e.currentTarget.getAttribute('href')
    setHash(window.location.hash)
  }

  // render components matches with route
  const renderComponents = () => {
    if( stateHash == '' || stateHash == '#welcome' ){
      return <Welcome/>
    }else if( stateHash == '#woocommerce' ){
      return <Woocommerce/>
    }else if( stateHash == '#widgets' ){
      return <Elements/>
    }else if( stateHash == '#modules' ){
      return <Modules/>
    }else{
      return <p>No route matches!</p>
    }
  }

  return (
      <main>
        <Navs navTo={navTo} hash={stateHash}/>
        <Notices/>
        <div class="sb-panel-wrapper">
          {renderComponents()}
        </div>
        <ToastContainer/>
      </main>
  );
};

export default App;
