import React, { act } from 'react'
import { Button, Modal } from '@wordpress/components';
import { useState } from '@wordpress/element';
import { Icon, info } from '@wordpress/icons';

function Widget1({
    id = 'widget_', 
    isPro = true, 
    title = 'Widget Title', 
    description = '', 
    settings = false, 
    toggle = true, 
    onChange = () => {},
    defaultValue = false,
    active = false
}){
    const [ isOpen, setOpen ] = useState( false );
    const openModal = () => setOpen( true );
    const closeModal = () => setOpen( false );

    const checkIsProEnable = (e) => {
        if(!active){
            e.preventDefault();
            openModal();
        }
    }

    const handleChange = (e) => {
        if(!active){
            e.preventDefault();
            openModal();
        }else{
            onChange(e);
        }
    }
    return (
        <>
        { isOpen && (
            <Modal onRequestClose={ closeModal } title='Need Pro?'>
                <div className="pure-pro-modal-wrapper">
                    <div className="pure-pro-modal-header">
                        <Icon icon={info} size={100} fill='coral'/>
                        <h2>Upgrade to Pro</h2>
                    </div>
                    <div className="pure-pro-modal-content">
                        <p>Upgrade to Pro to unlock this feature.</p>
                        <Button isPrimary href='https://themepure.net/plugins/storebuild/pricing/' target='_blank'>
                            Upgrade to Pro
                        </Button>
                    </div>
                </div>
            </Modal>
        ) }
        <div className="sb-widget-setting-item sb-widget-setting-item-2 sb-d-flex sb-justify-content-between sb-align-items-center sb-flex-wrap" onClick={checkIsProEnable}>
            {isPro && <div className="sb-widget-setting-badge">
                <span>Pro</span>
            </div>}
            <div className="sb-widget-setting-content sb-d-flex sb-align-items-center">
                <h3 className="sb-widget-setting-title">{ title }</h3>
                {description && <div className="sb-widget-tooltip-wrapper">
                    <span>
                    <svg width={15} height={15} viewBox="0 0 15 15" fill="none" xmlns="http://www.w3.org/2000/svg">
                        <path opacity="0.2" d="M7.5 0.5C11.3511 0.5 14.5 3.64886 14.5 7.5C14.5 11.3511 11.3511 14.5 7.5 14.5C3.64886 14.5 0.5 11.3511 0.5 7.5C0.5 3.64886 3.64886 0.5 7.5 0.5Z" stroke="currentColor" strokeLinecap="round" strokeLinejoin="round" />
                        <path d="M7.49902 10.3167V7.06665" stroke="currentColor" strokeWidth="1.5" strokeLinecap="round" strokeLinejoin="round" />
                        <path d="M7.49512 3.89996H7.50096" stroke="currentColor" strokeWidth="1.5" strokeLinecap="round" strokeLinejoin="round" />
                    </svg>
                    </span>
                    <div className="sb-widget-tooltip-content">
                    <p>{description}</p>
                    </div>
                </div>}
            </div>
            <div className="sb-widget-setting-action">
                {settings && <div className="sb-widget-setting-offcanvas">
                    <button type="button" className="sb-offcanvas-open-btn" onClick={handleChange}>
                    <span>
                        <svg width={18} height={17} viewBox="0 0 18 17" fill="none" xmlns="http://www.w3.org/2000/svg">
                        <path opacity="0.76" d="M8.99743 11.2132C10.3223 11.2132 11.3962 10.1104 11.3962 8.74998C11.3962 7.38959 10.3223 6.28677 8.99743 6.28677C7.67261 6.28677 6.59863 7.38959 6.59863 8.74998C6.59863 10.1104 7.67261 11.2132 8.99743 11.2132Z" stroke="currentColor" strokeMiterlimit={10} strokeLinecap="round" strokeLinejoin="round" />
                        <path d="M1 9.47254V8.02746C1 7.17355 1.67966 6.46743 2.51924 6.46743C3.96652 6.46743 4.55822 5.41646 3.83058 4.12738C3.41479 3.38841 3.66267 2.42776 4.3903 2.0008L5.77361 1.18794C6.4053 0.802041 7.22089 1.03194 7.5967 1.68059L7.68466 1.83659C8.4043 3.12567 9.58771 3.12567 10.3153 1.83659L10.4033 1.68059C10.7791 1.03194 11.5947 0.802041 12.2264 1.18794L13.6097 2.0008C14.3373 2.42776 14.5852 3.38841 14.1694 4.12738C13.4418 5.41646 14.0335 6.46743 15.4808 6.46743C16.3123 6.46743 17 7.16533 17 8.02746V9.47254C17 10.3265 16.3203 11.0326 15.4808 11.0326C14.0335 11.0326 13.4418 12.0835 14.1694 13.3726C14.5852 14.1198 14.3373 15.0722 13.6097 15.4992L12.2264 16.3121C11.5947 16.698 10.7791 16.4681 10.4033 15.8194L10.3153 15.6634C9.5957 14.3743 8.41229 14.3743 7.68466 15.6634L7.5967 15.8194C7.22089 16.4681 6.4053 16.698 5.77361 16.3121L4.3903 15.4992C3.66267 15.0722 3.41479 14.1116 3.83058 13.3726C4.55822 12.0835 3.96652 11.0326 2.51924 11.0326C1.67966 11.0326 1 10.3265 1 9.47254Z" stroke="currentColor" strokeMiterlimit={10} strokeLinecap="round" strokeLinejoin="round" />
                        </svg>
                    </span>
                    </button>
                </div>}
                {toggle && <label className="sb-widget-setting-switch" htmlFor={id} onClick={checkIsProEnable}>
                    { defaultValue && <input 
                                    type="checkbox" 
                                    id={id} 
                                    onChange={handleChange} 
                                    checked 
                                    name={id} 
                                    disabled={active? false:true}/>
                    }
                    { !defaultValue && <input 
                                    type="checkbox" 
                                    id={id} 
                                    onChange={handleChange} 
                                    name={id} 
                                    disabled={active? false:true}/>
                    }
                    <span/>
                </label>}
            </div>
        </div>
        </>
    )
}

function Widget2({
    id = 'widget_', 
    isPro = true, 
    title = 'Widget Title', 
    style = 1, 
    onChange = () => {},
    defaultValue = false,
    active = false
}){
    console.log(defaultValue);
    const [ isOpen, setOpen ] = useState( false );
    const openModal = () => setOpen( true );
    const closeModal = () => setOpen( false );

    const checkIsProEnable = (e) => {
        if(!active && isPro){
            e.preventDefault();
            openModal();
        }
    }
    const handleChange = (e) => {
        if(!active && isPro){
            e.preventDefault();
            openModal();
        }else{
            onChange(e);
        }
    }
    return (
        <>
        { isOpen && (
            <Modal onRequestClose={ closeModal } title='Need Pro?'>
                <div className="pure-pro-modal-wrapper">
                    <div className="pure-pro-modal-header">
                        <Icon icon={info} size={100} fill='coral'/>
                        <h2>Upgrade to Pro</h2>
                    </div>
                    <div className="pure-pro-modal-content">
                        <p>Upgrade to Pro to unlock this feature.</p>
                        <Button isPrimary href='https://themepure.net/plugins/storebuild/pricing/' target='_blank'>
                            Upgrade to Pro
                        </Button>
                    </div>
                </div>
            </Modal>
        ) }
        <div className="sb-widget-setting-item sb-d-flex sb-justify-content-between sb-align-items-center sb-flex-wrap" onClick={checkIsProEnable}>
            {(isPro && style === 1) && <div className="sb-widget-setting-badge">
                <span>Pro</span>
            </div>}
            <div className="sb-widget-setting-content">
                <h3 className="sb-widget-setting-title">{title}{(isPro && style === 2) && <span>Pro</span>}</h3>
            </div>
            <div className="sb-widget-setting-action">
                <label className="sb-widget-setting-switch" htmlFor={id} onClick={checkIsProEnable}>
                    { defaultValue && <input 
                                    type="checkbox" 
                                    id={id} 
                                    onChange={handleChange} 
                                    checked 
                                    name={id} 
                                    disabled={(isPro && !active)? true:false}/>
                    }
                    { !defaultValue && <input 
                                    type="checkbox" 
                                    id={id} 
                                    onChange={handleChange} 
                                    name={id} 
                                    disabled={(isPro && !active)? true:false}/>
                    }
                    <span/>
                </label>
            </div>
        </div>
        </>
    )
}

function Widget({ 
    id = 'widget_', 
    onChange= () => {}, 
    widget = 1, 
    isPro = true, 
    title = 'Widget Title', 
    description = '', 
    settings = false, 
    toggle = true, 
    style = 1,
    defaultValue = false,
    active = false
}) {
  return (
    <>
        {   widget === 1? 
            <Widget1 
                id={id} 
                isPro={isPro} 
                title={title} 
                description={description} 
                settings={settings} 
                toggle={toggle} 
                onChange={onChange}
                defaultValue={defaultValue}
                active={active}
            /> : 
            <Widget2 
                id={id} 
                isPro={isPro} 
                title={title} 
                style={style} 
                onChange={onChange}
                defaultValue={defaultValue}
                active={active}
            />
        }
    </>
  )
}

export default Widget