import React from 'react'
import { useState } from '@wordpress/element'
import { Button } from '@wordpress/components';
import DismissibleVideoTutorial from '../media/DismissibleVideo';

function Offcanvas({ 
    open = false, 
    onClose = () => {},
    heading = 'Settings',
    children,
    onSave = () => {},
    onReset = () => {},
    video = '',
    id = '',
    docs = ''
}) {

    return (
        <>
            {/* offcanvas area start */}
            <div className={ open? "sb-offcanvas-area sb-offcanvas-opened" : "sb-offcanvas-area" }>
                <div className="sb-offcanvas-wrapper sb-d-flex sb-flex-column sb-justify-content-between">
                    <div className="sb-offcanvas-main">
                    <div className="sb-offcanvas-header sb-d-flex sb-align-items-center sb-justify-content-between">
                        <div className="sb-d-flex sb-align-items-center">
                            <button className="sb-offcanvas-close-btn" onClick={onClose}>
                            <svg width={12} height={12} viewBox="0 0 12 12" fill="none" xmlns="http://www.w3.org/2000/svg">
                                <path d="M11 1L1 11" stroke="currentColor" strokeWidth="1.5" strokeLinecap="round" strokeLinejoin="round" />
                                <path d="M1 1L11 11" stroke="currentColor" strokeWidth="1.5" strokeLinecap="round" strokeLinejoin="round" />
                            </svg>
                            </button>
                            <p>{heading}</p>
                        </div>
                        { docs && <a href={docs} target='_blank'>Read Docs</a> }
                    </div>
                    <div className="sb-offcanvas-content">
                        { video && <DismissibleVideoTutorial
                            moduleId={id}
                            videoUrl={video}
                        /> }
                        { children }
                    </div>
                    </div>
                    <div className="sb-offcanvas-footer">
                    <div className="sb-offcanvas-footer-btn-wrapper sb-d-sm-flex sb-align-items-center">
                        <a href="#" className="sb-offcanvas-btn-reset" onClick={onReset}>Reset to Default</a>
                        <a href="#" className="sb-offcanvas-btn-fill" onClick={onSave}>Save Settings</a>
                    </div>
                    </div>
                </div>
            </div>
            <div className={ open? "sb-body-overlay opened" : "sb-body-overlay" } onClick={onClose}/>
            {/* offcanvas area end */}
        </>
    )
}

export default Offcanvas