import React from 'react'
import TabHead from '../Sections/TabHead'
import {
    WidgetInput,
    WidgetDropdown
} from '../widgets'
import { toast } from 'react-toastify'
import axios from 'axios'

function Woocommerce() {
    const templatesList = pure_wc_shopbuild.templates;
    // console.log(templatesList);
    const noTemplate = [
        {
            value: 0,
            label: 'No Template',
            type: 'none',
            selected: true
        }
    ];
    
    let pro_modules   = {
        myaccountlogintemplate: true,
        myaccounttemplate: true,
        thankyoutemplate: true
    };
    if(!_.isEmpty(pure_wc_shopbuild.pro_modules)){
        pro_modules = pure_wc_shopbuild.pro_modules;
    }
    const myaccountLogin = pro_modules?.myaccountlogintemplate;
    const myaccount = pro_modules?.myaccounttemplate;
    const thankyou  = pro_modules?.thankyoutemplate;

    const [settings, setSettings] = React.useState(pure_wc_shopbuild.settings._pure_shopbuild_woocommerce)

    const handleChange = (e) => {
        e.preventDefault()
        setSettings({...settings, [e.target.name]:e.target.value})
    }
    const handleSave = (e) => {
        e.preventDefault()
        const form_data = new FormData()
        form_data.append('action', 'pure_wc_shopbuild_save')
        form_data.append('security', pure_wc_shopbuild.nonce)
        form_data.append('_pure_shopbuild_woocommerce', JSON.stringify(settings))
        axios.post(pure_wc_shopbuild.ajax_url, form_data)
        .then( res => {
            if( res.data?.success === false ){
                toast.error(res.data?.data?.message)
                return;
            }
            toast.success(res.data?.data?.message)
        })
        .catch( err => {
            toast.error(res.data?.data?.message)
        })
    }

    let shoppageTemplateList = templatesList.map( templ => {
        if(templ.value == settings.pure_wc_shop_page_id){
            return {...templ, selected:true}
        }else{
            return {...templ, selected:false}
        }
    })

    shoppageTemplateList = shoppageTemplateList.filter( tmpl => tmpl.type == 'shop');
    shoppageTemplateList = [...noTemplate, ...shoppageTemplateList];


    let singlepageTemplateList = templatesList.map( templ => {
        if(templ.value == settings.pure_wc_single_page_id){
            return {...templ, selected:true}
        }else{
            return {...templ, selected:false}
        }
    })

    singlepageTemplateList = singlepageTemplateList.filter( tmpl => tmpl.type == 'single');
    singlepageTemplateList = [...noTemplate, ...singlepageTemplateList];

    let cartpageTemplateList = templatesList.map( templ => {
        if(templ.value == settings.pure_wc_cart_page_id){
            return {...templ, selected:true}
        }else{
            return {...templ, selected:false}
        }
    })
    cartpageTemplateList = cartpageTemplateList.filter( tmpl => tmpl.type == 'cart');
    cartpageTemplateList = [...noTemplate, ...cartpageTemplateList];

    let checkoutpageTemplateList = templatesList.map( templ => {
        if(templ.value == settings.pure_wc_checkout_page_id){
            return {...templ, selected:true}
        }else{
            return {...templ, selected:false}
        }
    })
    checkoutpageTemplateList = checkoutpageTemplateList.filter( tmpl => tmpl.type == 'checkout');
    checkoutpageTemplateList = [...noTemplate, ...checkoutpageTemplateList];

    let thankyoupageTemplateList = templatesList.map( templ => {
        if(templ.value == settings.pure_wc_thankyou_page_id){
            return {...templ, selected:true}
        }else{
            return {...templ, selected:false}
        }
    })
    thankyoupageTemplateList = thankyoupageTemplateList.filter( tmpl => tmpl.type == 'thankyou');
    thankyoupageTemplateList = [...noTemplate, ...thankyoupageTemplateList];

    let myAccountPageTemplateList = templatesList.map( templ => {
        if(templ.value == settings.pure_wc_my_account_page_id){
            return {...templ, selected:true}
        }else{
            return {...templ, selected:false}
        }
    })
    myAccountPageTemplateList = myAccountPageTemplateList.filter( tmpl => tmpl.type == 'myaccount');
    myAccountPageTemplateList = [...noTemplate, ...myAccountPageTemplateList];

    let myAccountLoginPageTemplateList = templatesList.map( templ => {
        if(templ.value == settings.pure_wc_my_account_login_page_id){
            return {...templ, selected:true}
        }else{
            return {...templ, selected:false}
        }
    })
    myAccountLoginPageTemplateList = myAccountLoginPageTemplateList.filter( tmpl => tmpl.type == 'myaccountlogin');
    myAccountLoginPageTemplateList = [...noTemplate, ...myAccountLoginPageTemplateList];

    return (
        <>
        {/* <!-- elements content tab --> */}
        <div class="sb-woo-tab-content">
            {/* Controls Top setting all */}
            <div className="sb-controls-top">
                <TabHead title='WooCommerce Settings' text='You can enable or disable all options by one click.' onSave={handleSave}/>
            </div>
            {/* woo options list */}
            <div className="sb-woo-option-wrapper">
                <div className="sb-container">
                <div className="sb-woo-option-inner">
                    <div className="sb-row">
                    <div className="sb-col-xl-12">
                        <div className="sb-woo-option-item-wrapper">
                            <WidgetInput
                                label='Products Limit'
                                text='You can handle the product limit for the Shop page.'
                                type='number'
                                defaultValue={settings.pure_wc_products_limits}
                                id='pure_wc_products_limits'
                                onChange={handleChange}
                            />
                            <WidgetDropdown
                                label='Shop Page Template'
                                text='You can select a custom template for the Shop page layout.'
                                data={shoppageTemplateList}
                                disabled={false}
                                id='pure_wc_shop_page_id'
                                onChange={handleChange}
                            />
                            <WidgetDropdown
                                label='Single Product Template'
                                text='You can select a custom template for the product details page layout.'
                                data={singlepageTemplateList}
                                disabled={false}
                                id='pure_wc_single_page_id'
                                onChange={handleChange}
                            />

                            <WidgetDropdown
                                label='Cart Page Template'
                                text='You can select a template for the cart page.'
                                isPro={false}
                                data={cartpageTemplateList}
                                disabled={false}
                                id='pure_wc_cart_page_id'
                                onChange={handleChange}
                            />

                            <WidgetDropdown
                                label='Checkout Page Template'
                                text='You can select a template for the checkout page.'
                                isPro={false}
                                data={checkoutpageTemplateList}
                                disabled={false}
                                id='pure_wc_checkout_page_id'
                                onChange={handleChange}
                            />
                            <WidgetDropdown
                                label='Thank You Page Template'
                                text='Select a template for the Thank you page layout.'
                                isPro={thankyou}
                                data={thankyoupageTemplateList}
                                disabled={thankyou}
                                id='pure_wc_thankyou_page_id'
                                onChange={handleChange}
                            />

                            <WidgetDropdown
                                label='My Account Page Template'
                                text='Select a template for the my account page layout.'
                                isPro={myaccount}
                                data={myAccountPageTemplateList}
                                disabled={myaccount}
                                id='pure_wc_my_account_page_id'
                                onChange={handleChange}
                            />

                            <WidgetDropdown
                                label='My Account Login Page Template'
                                text='Select a template for the my account login page layout.'
                                isPro={myaccountLogin}
                                data={myAccountLoginPageTemplateList}
                                disabled={myaccountLogin}
                                id='pure_wc_my_account_login_page_id'
                                onChange={handleChange}
                            />
                        </div>
                    </div>
                    </div>
                </div>
                </div>
                <div className="sb-woo-option-save sb-d-flex sb-justify-content-end">
                    <a href="#" className="sb-btn-fill" onClick={handleSave}>Save Settings</a>
                </div>
            </div>
        </div>
        </>
    )
}

export default Woocommerce